import React, { useState, useEffect } from 'react'
import './App.css';
import { makeStyles, CssBaseline, createMuiTheme, ThemeProvider } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Toolbar from '@material-ui/core/Toolbar';
import Link from '@material-ui/core/Link';
import Logo from '../images/logo.jpg';
import BWMain from "../components/BWMain";
import BWWarning from "../components/BWWarning";
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Container p={3}>
          {children}
        </Container>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#333996",
      light: '#3c44b126'
    },
    secondary: {
      main: "#f83245",
      light: '#f8324526'
    },
    background: {
      default: "#f4f5fd"
    },
  },
  overrides: {
    MuiAppBar: {
      root: {
        transform: 'translateZ(0)'
      }
    }
  },
  props: {
    MuiIconButton: {
      disableRipple: true
    }
  },
  spacing:1,
})
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));



function App() {
  
  const classes = useStyles();
  
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <ThemeProvider theme={theme} >
      <div className={classes.root}>
          <Box mt={2}>
              <AppBar position="static" style={{minHeight:'80px'}}>
                <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
                  <Tab label="Crypto Wallet" {...a11yProps(0)} />
                  <Tab label="Smart Contract" {...a11yProps(1)} />
                  <Tab label="Liquidity Snipe" {...a11yProps(2)} />
                  <Tab label="CARDANO" {...a11yProps(3)} />
                </Tabs>
              </AppBar>
          </Box>
          <Box  mt={55}>
              <TabPanel value={value} index={0}>
                    {/* <img src={Logo} /> */}
                    <Typography variant="h4" color="textSecondary" align="center" gutterBottom>
                              Helium Brainwallet
                    </Typography>
                    <Typography variant="body2" color="textSecondary" align="center" gutterBottom>
                              built by David in Aug 2021
                    </Typography>
                    <Typography variant="h6" color="textSecondary" gutterBottom>
                              #1, The phrase that is genreated automatically is hard to be remembered, but your own phrase is good.
                    </Typography>
                    <Typography variant="h6" color="textSecondary" gutterBottom>
                              #2, The last word of 12 mnemonic words is serving as checksum, but it's always '0000' for Mobile which is not strictly following BIP39.
                    </Typography>
                    <Typography variant="h6" color="textSecondary" gutterBottom>
                              #3, This has been verified on wallets of <Link href="https://github.com/helium/helium-wallet-rs/releases/tag/v1.6.5" target="_blank">CLI</Link> and  Mobile(
                              <Link href="https://apps.apple.com/us/app/helium-hotspot/id1450463605" target="_blank">iOS</Link> and 
                              <Link href="https://play.google.com/store/apps/details?id=com.helium.wallet" target="_blank">Android</Link>).
                    </Typography>
                    <BWMain />
                    
                    <BWWarning open2={true}/>
              </TabPanel>
              <TabPanel value={value} index={1}>
                TODO: NFT
              </TabPanel>
              <TabPanel value={value} index={2}>
                TODO: Liquidity Snipe
              </TabPanel>
              <TabPanel value={value} index={3}>
                TODO: Every thing about CARDANO
              </TabPanel>
          </Box>
      </div>

      <CssBaseline />
    </ThemeProvider>
  );
}

export default App;
