import React, {useEffect} from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6" color='secondary'>{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function CustomizedDialogs(props) {
    
    const {open2} = props;
    const [open, setOpen] = React.useState(open2);

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          WARNING!
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
                This app does NOT save your Heium wallet information! This app will automatically reset and delete all history in 20 minutes! 
                You are responsible for saving your Phrase, your SALT (usually four digits), and the SALT placement! 
                If you lose this information, you will NEVER be able to recover your Helim wallet!
          </Typography>
          <Typography gutterBottom>
                By clicking the CONTINUE button below, you are agreeing to all Terms and Conditions located here, Mobile-Currency.com/ICE/terms
          </Typography>
          <Typography gutterBottom>
                Begin at <b>Step 1</b>, Erase the text inside the box and type your secret words. NOTE: Spacing, capitalization, and punctuation matter!
          </Typography>
          <Typography gutterBottom>
                Then at <b>step 2</b>, BE VERY CAREFUL TO NOT REMOVE ANY CHARACTERS! Add your SALT here.
          </Typography>
          <Typography gutterBottom>
                As a result, you will now see your Helium public wallet address. Use a QR Code scanning app on your mobile phone to save your wallet address.
          </Typography>
          <Typography gutterBottom color='error'>
                Repeat these steps several times to be certain that you get the EXACT same result. SAVE YOUR SECRET WORDS and the SALT in a secure location!
          </Typography>
          
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Continue
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
