import React from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import InputAdornment from '@material-ui/core/InputAdornment';
import GTranslate from '@material-ui/icons/GTranslate';
import Pets from '@material-ui/icons/Pets';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import MD5 from 'md5.js';
import * as bip39 from 'bip39';
import * as bs58 from 'bs58check';
import tweetnacl from 'tweetnacl';
import BWQRCode from './BWQRCode';
import BWTimer from './BWTimer';
import BWEntropyFromMnemonic from './BWEntropyFromMnemonic';
import {fromEntropy, fromMnemonic} from './BWMobile';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '85%',
    },
  },
}));

export default function StateTextFields() {
    const classes = useStyles();

    const [mnemonic, setMnemonic] = React.useState('');
    const [saltedMD5, setSaltedMD5] = React.useState('');
    const [walletAddress, setWalletAddress] = React.useState('');
    const [qrcodeText, setQrcodeText] = React.useState('');
    const [words12, setWords12] = React.useState('');
    const [walletType, setWalletType] = React.useState('mobile');

    const hexByteStringToByteArray=(hexByteString) => {
        var bytes = new Array(16); // this could also be a new Uint8array(16)
        for (var i = 0; i < hexByteString.length;) {
            var hexByte = hexByteString[i++] + hexByteString[i++];
            var byte = parseInt(hexByte, 16);
            bytes[i / 2 - 1] = byte;
        }
        return bytes;
    }

    const generate12words = (saltedMD5, eventValue) => {
        const bits=hexByteStringToByteArray(md5Func(saltedMD5));

        let wt=walletType;
        if(eventValue){
            wt=eventValue;
        }

        if(wt==='cli'){
            // For CLI
            const words=bip39.entropyToMnemonic(bits);
            return words;
        }else if(wt==='mobile'){
            // For mobile
            const words_mobile = fromEntropy(bits);
            return words_mobile.join(' ');
        }
    }

    const md5Func = (str) => {
        const md5d=new MD5();
        md5d.update(str);
        let hex=md5d.digest('hex');
        return hex;
    }

    const insert = (arr, index, newItem) => [
        // part of the array before the specified index
        ...arr.slice(0, index),
        // inserted item
        newItem,
        // part of the array after the specified index
        ...arr.slice(index)
      ];
  
    const handleChangeWalletType = (event) => {
        setWalletType(event.target.value); // walletType is not immediately updated after this
        
        let md5_2=md5Func(saltedMD5);

        /*
        walletType is not immediately updated after 'setWalletType(event.target.value)', that's why we pass in 'event.target.valueâ€™
        */
        let words=generate12words(md5_2, event.target.value);
        setWords12(words);
        let walletAddress = showWalletAddress(words);
        setWalletAddress(walletAddress);
    };
    
    const handleChangeUserPhrase = (event) => {
        setMnemonic(event.target.value);
        let md5_1=md5Func(mnemonic);
        setSaltedMD5(md5_1);
        let md5_2=md5Func(md5_1);
        let words=generate12words(md5_2);
        setWords12(words);
        let walletAddress = showWalletAddress(words);
        setWalletAddress(walletAddress);
    };

    
    const handleChangeMD5 = (event) => {
        setSaltedMD5(event.target.value);
        let md5_2=md5Func(event.target.value);
        let words=generate12words(md5_2);
        setWords12(words);
        let walletAddress = showWalletAddress(words);
        setWalletAddress(walletAddress);
    };

    const showWalletAddress = (words) => {
        // let seed=Buffer.from(hex+hex, 'hex');
        let seed2 = BWEntropyFromMnemonic(words);
        let keyPair = tweetnacl.sign.keyPair.fromSeed(seed2);
        var array_public = Array.from(keyPair.publicKey);
        array_public=insert(array_public, 0,1);
        array_public=insert(array_public, 0,0);

        let walletAddress = bs58.encode(Buffer.from(array_public));
        setQrcodeText(walletAddress);

        return walletAddress;
    }

    const myCallback = ()=>{
        setMnemonic('');
        setSaltedMD5('');
        setWalletAddress('');
        setQrcodeText('');
        setWords12('');
        setWalletType('mobile');
    }

    return (
        <div className={classes.root} noValidate autoComplete="off" >
            <div>
                <BWTimer callback={myCallback} timeout={1200} size={80}/>
                
                <Typography variant="subtitle2" gutterBottom>
                    Auto delete and restart in...
                </Typography>
                
            </div>

            <div>
                <RadioGroup row aria-label="wallet type" name="walletType" value={walletType} onChange={handleChangeWalletType}>
                    <FormControlLabel value="mobile" control={<Radio />} label="Mobile Wallet" />
                    <FormControlLabel value="cli" control={<Radio />} label="CLI Wallet" />
                </RadioGroup>
            </div>
            <div>
                <TextField id="standard-name" 
                label="Step 1. Your secret words" 
                placeholder=""
                helperText="Please enter your own mnemonic phrase here"
                value={mnemonic} 
                onChange={handleChangeUserPhrase} 
                InputLabelProps={{style: {fontSize: 25}}}         
                InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <GTranslate color="secondary" />
                      </InputAdornment>
                    ),
                  }}
                />
            </div>
            <div>
                <TextField
                id="filled-name"
                label="Step 2. Salting"
                placeholder=""
                helperText="DO NOT REMOVE ANY CHARACTERS, space matters, recommend to ad your SALT to the End or the Begining of this Field"
                value={saltedMD5}
                onChange={handleChangeMD5}
                InputLabelProps={{style: {fontSize: 25}}} 
                InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Pets color="secondary" />
                      </InputAdornment>
                    ),
                  }}
                />
            </div>
            <br/>
            <div>
                <TextField
                id="outlined-name"
                label="Result 1. Wallet 12 Words"
                value={words12}
                InputLabelProps={{style: {fontSize: 22}}} 
                helperText="Helium official wallet mnemonic 12 Words, this help you recover your wallet)"
                />
            </div>
            <div>
                <TextField
                label="Result 2. Wallet Address"
                value={walletAddress}
                InputLabelProps={{style: {fontSize: 22}}} 
                helperText="Helium wallet public address (share to receive token)"
                />
            </div>
            <div>
                <Typography variant="subtitle2" gutterBottom>
                    QR Code for your wallet address.
                </Typography>
                <BWQRCode text={qrcodeText}/>
            </div>
        </div>
    );
}